  const currentContent = content[lang as keyof typeof content] || content.en;

  return (
    <div className="min-h-screen pt-4 pb-12 px-4 bg-secondary/20">
      <div className="container mx-auto max-w-4xl">
        {/* Top back button */}
        <Button
          variant="ghost"
          size="icon"
          onClick={() => navigate(-1)}
          className="h-10 w-10 mb-4 rounded-md bg-background shadow-sm border border-border/50"
        >
          <ArrowLeft className="h-5 w-5" />
        </Button>

        <Card>
          <CardHeader>
            <CardTitle className="text-3xl font-bold">{currentContent.title}</CardTitle>
            <p className="text-muted-foreground">{currentContent.lastUpdated}</p>
          </CardHeader>
          <CardContent className="space-y-6">
            {currentContent.sections.map((section, index) => (
              <div key={index}>
                <h2 className="text-xl font-semibold mb-2">{section.title}</h2>
                <p className="text-muted-foreground leading-relaxed">{section.content}</p>
              </div>
            ))}
          </CardContent>
        </Card>
      </div>
    </div>
  );
